.movie_header_gen{
    background: linear-gradient(to bottom right, rgba(31.5, 10.5, 10.5, .5), rgba(31.5, 10.5, 10.5, 0.84));
    background-size: 100vw 100%;
    background-repeat: no-repeat;
}

.movie_header_content{
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-wrap: wrap;
    color: white;
}
.movie_header{
    padding: 50px 0;
}
.movie_img{
    height:60vh;
    margin: 0 100px 0 0;
}
.movie_title{
    font-size: 40px;
}
.movie_info_1{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
}
.movie_info_1 li{
    margin: 0 10px;
}

.movie_certif{
    color: rgb(255, 119, 0);
    margin: 0 10px 0 0;
    border: 2px solid rgb(255, 111, 0);
    padding: 2px;
    list-style: none;
}
.release_data{
    list-style: none;
    font-weight: 400;
    font-size: 20px;
}
.movie_genres{
    font-weight: 600;
    font-size: 17px;
    padding: 3px;
    border-bottom: 3px solid rgb(255, 111, 0);
}
